"use client"

import { useState, useEffect } from "react"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Sparkles, Moon, Play } from "lucide-react"

interface MonthCalendarProps {
  month: string
  days: number
  year: number
  userEmail: string
  monthIndex: number
}

export function MonthCalendar({ month, days, year, userEmail, monthIndex }: MonthCalendarProps) {
  const [readings, setReadings] = useState<any[]>([])

  const fetchReadings = async () => {
    try {
      const res = await fetch(`/api/reading-data?email=${userEmail}`)
      const data = await res.json()
      setReadings(data.data || [])
    } catch (error) {
      console.error("Erro ao buscar leituras:", error)
    }
  }

  useEffect(() => {
    if (userEmail) {
      fetchReadings()
    }
  }, [userEmail, monthIndex, year])

  const handleStartReading = async (day: number) => {
    const bookName = window.prompt("Qual livro voc√™ est√° come√ßando a ler?")
    if (!bookName) return

    try {
      const res = await fetch("/api/reading-data", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ 
          email: userEmail, 
          bookName, 
          action: "START_READING" 
        }),
      })

      if (res.ok) {
        fetchReadings()
      }
    } catch (error) {
      console.error("Erro ao iniciar leitura:", error)
    }
  }

  const handleFinishReading = async (bookName: string) => {
    try {
      const res = await fetch("/api/reading-data", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ 
          email: userEmail, 
          bookName, 
          action: "FINISH_READING" 
        }),
      })

      if (res.ok) {
        fetchReadings()
      }
    } catch (error) {
      console.error("Erro ao finalizar leitura:", error)
    }
  }

  return (
    <Card className="border-accent/20 bg-card p-6 shadow-lg md:p-10">
      <div className="mb-8 text-center">
        <div className="mb-2 flex items-center justify-center gap-2">
          <Sparkles className="h-5 w-5 text-primary" />
          <h2 className="font-serif text-3xl font-bold text-foreground md:text-4xl">{month}</h2>
          <Sparkles className="h-5 w-5 text-primary" />
        </div>
        <p className="text-lg text-muted-foreground">{year}</p>
      </div>

      <div className="grid grid-cols-2 gap-3 md:grid-cols-7 md:gap-4">
        {Array.from({ length: days }, (_, i) => i + 1).map((day) => {
          const currentDate = new Date(year, monthIndex, day)
          currentDate.setHours(12, 0, 0, 0)

          const activeReading = readings.find((r) => {
            const start = new Date(r.start_date)
            const end = r.end_date ? new Date(r.end_date) : new Date()
            start.setHours(0, 0, 0, 0)
            end.setHours(23, 59, 59, 999)
            return currentDate >= start && currentDate <= end
          })

          const isLendo = activeReading && activeReading.status === "lendo"
          const isConcluido = activeReading && activeReading.status === "concluido"

          return (
            <div
              key={day}
              className={`group relative flex min-h-[110px] flex-col rounded-lg border p-3 transition-all ${
                isLendo ? "border-green-500/50 bg-green-500/5 shadow-md" : 
                isConcluido ? "border-blue-500/30 bg-blue-500/5" : "border-border bg-background/50"
              }`}
            >
              <div className="mb-2 flex items-center justify-between">
                <span className={`font-semibold ${activeReading ? "text-primary" : "text-muted-foreground"}`}>
                  {day}
                </span>
                {day % 7 === 0 && <Moon className="h-3 w-3 text-muted-foreground/40" />}
              </div>

              <div className="mt-auto flex flex-col gap-1">
                {activeReading ? (
                  <div className={`rounded p-1.5 text-center text-[10px] font-bold leading-tight ${
                    isLendo ? "bg-green-600 text-white" : "bg-blue-100 text-blue-700 border border-blue-200"
                  }`}>
                    {isLendo ? "üìñ " : "‚úì "} {activeReading.book_name}
                    {isLendo && (
                      <button 
                        onClick={() => handleFinishReading(activeReading.book_name)}
                        className="mt-1 block w-full rounded bg-white/20 py-0.5 text-[9px] hover:bg-white/40"
                      >
                        Finalizar
                      </button>
                    )}
                  </div>
                ) : (
                  <Button
                    variant="ghost"
                    size="sm"
                    className="h-8 w-full border border-dashed border-border text-[10px] text-muted-foreground hover:bg-green-500/10 hover:text-green-600"
                    onClick={() => handleStartReading(day)}
                  >
                    <Play className="mr-1 h-3 w-3" /> Iniciar
                  </Button>
                )}
              </div>
            </div>
          )
        })}
      </div>

      <div className="mt-8 flex items-center justify-center gap-2 text-xs text-muted-foreground">
        <div className="h-px w-12 bg-border" />
        <span>‚ú¶</span>
        <div className="h-px w-12 bg-border" />
      </div>
    </Card>
  )
}